<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="categories"
      :search="search"
      v-model="selected"
      show-select
      class="elevation-2 ma-2"
      :loading="loading"
      loading-text="Loading... Please wait"
    >
      <!-- <template v-slot:top>  -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title></v-toolbar-title>

          <v-divider class="mx-4" inset vertical>
            <!-- vertical  -->
          </v-divider>
          <v-btn
            class="red darken-2 white--text mr-2"
            @click="deleteAll"
            :disabled="selected.length <= 0"
          >
            <v-icon>mdi-delete</v-icon>
            Delete Selected
          </v-btn>
          <v-btn
            class="amber darken-2 white--text"
            @click="resetSelectedLevel"
            :disabled="selected.length <= 0"
          >
            <v-icon>mdi-restore</v-icon>
            Reset Selected Level
          </v-btn>
          <v-spacer></v-spacer>
          <!--  -->
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            class="mx-3"
          ></v-text-field>
          <!--     start -->
          <v-dialog v-model="dialog" max-width="800px">
            <!-- dialog  template  -->
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="success" dark class="mb-2" v-bind="attrs" v-on="on">
                <v-icon>mdi-plus</v-icon>
                New Item
              </v-btn>
            </template>
            <!-- dialog  </template>  -->
            <v-card>
              <!--      -->
              <v-card-title
                class="darken-2 white--text"
                :class="editedIndex === -1 ? 'teal' : 'amber'"
              >
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col :cols="formCols">
                      <!-- Title -->
                      <v-text-field
                        v-model="editedItem.title"
                        label="Category Title"
                        :counter="50"
                        clearable
                      ></v-text-field>
                    </v-col>
                    <v-col cols="2" v-if="editedIndex > -1">
                      <!--  level  type="number" -->
                      <v-text-field
                        v-model="editedItem.level"
                        label="Level"
                        value="0"
                        type="number"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12">
                      <!--  -->
                      <v-textarea
                        rows="4"
                        v-model="editedItem.description"
                        label="Category Description"
                        counter
                        outlined
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="success darken-1" text @click="save">
                  Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <!--     end -->
          <!--  start -->
          <v-dialog v-model="dialogDelete" max-width="500">
            <v-card>
              <v-card-title class="text-h5 red darken-2 white--text">
                Delete Item?
              </v-card-title>
              <v-card-text class="pa-8 text-h6">
                {{ deleteConfirmInfo }}
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">
                  Cancel
                </v-btn>
                <v-btn color="success darken-1" text @click="deleteItemConfirm">
                  OK
                </v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <!--  end -->
        </v-toolbar>
      </template>
      <!--   template  v-slot value="actions"  -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          dense
          class="mr-2"
          color="amber"
          @click="editItem(item)"
          title="Edit Item"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          color="red darken-2"
          dense
          @click="deleteItem(item)"
          title="Delete Item"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-row></v-row>
  </v-container>
</template>

<script>
import moment from "moment"; //
import CategoryService from "@/service/category_service.js";
export default {
  name: "cagegory-manager",
  data: () => ({
    loading: false, // 
    dialog: false, //     
    dialogDelete: false, // 
    deleteConfirmInfo: "", // 
    editedIndex: -1, //     -1 categories
    // 
    editedItem: {
      title: "",
      description: "",
      level: 0,
    },
    // 
    defaultItem: {
      title: "",
      description: "",
      level: 0,
    },
    page: 1,
    pageSize: 10,
    totalPages: 1,
    totalVisible: 10,
    pageItems: [10, 20, 30, 50, 100],
    categories: [], // 
    // ; text: value: 
    // sortable: align; width: 
    headers: [
      { text: "Title", value: "title", sortable: true, align: "start" },
      { text: "Description", value: "description", sortable: false },
      {
        text: "LastEditor",
        value: "lastEditor.username",
        sortable: true,
        width: 150,
      },
      { text: "Level", value: "level", sortable: true, width: 80 },
      { text: "Count", value: "topicCount", sortable: true, width: 90 },
      { text: "CreatedAt", value: "createdAt", sortable: true, width: 130 },
      { text: "UpdatedAt", value: "updatedAt", sortable: true, width: 130 },
      {
        text: "Actions",
        value: "actions",
        sortable: false,
        width: 80,
        align: "center",
      },
    ],
    selected: [], //
    search: "", // 
  }),
  // 
  mounted() {
    moment.locale("zh-cn"); // 
    this.getCategories();
  },
  methods: {
    // 
    getCategories() {
      this.loading = true; // 
      this.categories = []; // 
      CategoryService.fetchAll('level')
        .then((resp) => {
          for (let item of resp) {
            this.categories.push(this.fromJson(item));
          }
          console.log(this.categories);
          this.loading = false; // 
        })
        .catch((error) => {
          this.loading = false; // 
          //
        });
    },

    // 
    deleteItem(item) {
      console.log("deleteItem: ", item);
      this.editedIndex = this.categories.indexOf(item); // 
      this.editedItem = Object.assign({}, item); // 
      this.deleteConfirmInfo = "Are you sure you want to delete this item?";
      this.dialogDelete = true;
    },

    // 
    deleteAll() {
      console.log("delete all: ", this.selected);
      let count = this.selected.length;
      if (count) {
        this.deleteConfirmInfo = `Are you sure you want to delete ${count} items?`;
        this.dialogDelete = true;
      }
    },

    // 
    deleteItemConfirm() {
      if (this.selected.length) {
        CategoryService.deleteAll(this.selected)
          .then((resp) => {
            this.getCategories(); //
            this.selected = []; // 
          })
          .catch((error) => {
            //
          });
      } else {
        let item = this.categories.splice(this.editedIndex, 1)[0];
        console.log(item);
        CategoryService.delete(item.id)
          .then((resp) => {
            console.log("deleteItemConfirm: ", resp);
            //
          })
          .catch((error) => {
            //
          });
      }
      this.closeDelete();
    },

    // 
    closeDelete() {
      this.dialogDelete = false;
      // this.$nextTick() DOM 
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    // 
    editItem(item) {
      console.log("editItem: ", item);
      //  categories 
      this.editedIndex = this.categories.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    //    
    save() {
      if (this.editedIndex > -1) {
        //  updatedAtleancloudupdatedAt 
        this.editedItem.updatedAt = moment(Date.now()).format("lll");
        this.editedItem.lastEditor = CategoryService.currentUser(); // 
        // Object.assign()
        let item = Object.assign(
          this.categories[this.editedIndex],
          this.editedItem
        );

        /**
         * ,  id 
         * leancloud objectId,
         *  id  Class  id 
         */
        CategoryService.update(item.id, {
          title: item.title,
          description: item.description,
          level: Number(item.level), // 
          lastEditor: CategoryService.currentUser(), // 
        });
      } else {
        // 
        let category = {
          title: this.editedItem.title.trim(), //  
          description: this.editedItem.description, // 
          level: 0, // 
          count: 0, // 
          createdBy: CategoryService.currentUser(), // 
          lastEditor: CategoryService.currentUser(), // 
        };

        // new Category(this.editedItem.title.trim(), this.editedItem.description);
        if (category.title) {
          // 
          CategoryService.exists(category.title)
            .then((result) => {
              // 
              if (!result) {
                CategoryService.create(category)
                  .then((item) => {
                    // 
                    this.categories.push(this.fromJson(item));
                  })
                  .catch((error) => {
                    console.log(error);
                  });
              } else {
                console.log("the title is exists.");
              }
            })
            .catch((error) => {
              //
            });
        } else {
          console.log("the title is null.");
        }
      }
      this.close();
    },

    //   level  0 ,   
    resetSelectedLevel() {
      CategoryService.batchUpdate(this.selected, "level", 0)
        .then((resp) => {
          this.selected = []; // 
          console.log(resp);
          // this.getCategories();  // 
          for (let item of resp) {
            let index = this.categories.findIndex((c) => c.id === item.id);
            //  this.fromJson(item) 
            Object.assign(this.categories[index], this.fromJson(item));
          }
        })
        .catch((error) => {});
    },

    // 
    close() {
      this.dialog = false; // 
      // this.$nextTick() DOM 
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    // 
    fromJson(item) {
      return {
        id: item.id,
        title: item.get("title"),
        description: item.get("description"),
        level: item.get("level"),
        topicCount: item.get("count"),
        createdAt: moment(item.createdAt).format("lll"),
        updatedAt: moment(item.updatedAt).format("lll"),
        // 
        createdBy: item.get("createdBy")
          ? this.getUser(item.get("createdBy"))
          : "",
        lastEditor: item.get("lastEditor")
          ? this.getUser(item.get("lastEditor"))
          : "",
      };
    },

    // 
    getUser(user) {
      // console.log(user);
      return {
        id: user.id,
        username: user.get("username"),
        email: user.get("email"),
      };
    },
  },

  // 
  computed: {
    // 
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    //      Title 
    //  LevelTitle  cols="12"
    //  LevelTitle  cols="10"
    formCols() {
      return this.editedIndex === -1 ? 12 : 10;
    },
  },

  // 
  watch: {
    //  data dialogval 
    dialog(val) {
      val || this.close(); // 
    },

    dialogDelete(val) {
      val || this.close(); // 
    },
  },
};
</script>

